import React, { useState } from 'react';
import {
  Box,
  Tabs,
  Tab,
  useMediaQuery,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { useNavigate, useLocation } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import VisibilityIcon from '@mui/icons-material/Visibility';
import ModelTrainingIcon from '@mui/icons-material/ModelTraining';
import MenuIcon from '@mui/icons-material/Menu';

const navigationItems = [
  { path: '/', label: 'Home', icon: <HomeIcon /> },
  { path: '/analysis', label: 'Analysis', icon: <AnalyticsIcon /> },
  { path: '/visualizations', label: 'Visualizations', icon: <VisibilityIcon /> },
  { path: '/models', label: 'Models', icon: <ModelTrainingIcon /> },
];

const Navigation: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const navigate = useNavigate();
  const location = useLocation();
  const [mobileOpen, setMobileOpen] = useState(false);

  const handleNavigation = (path: string) => {
    navigate(path);
    setMobileOpen(false);
  };

  const currentPath = location.pathname;

  const renderMobileNav = () => (
    <>
      <Box sx={{ borderBottom: 1, borderColor: 'divider', bgcolor: 'white' }}>
        <IconButton
          color="primary"
          aria-label="open drawer"
          edge="start"
          onClick={() => setMobileOpen(true)}
          sx={{ m: 1 }}
        >
          <MenuIcon />
        </IconButton>
      </Box>
      <Drawer
        anchor="left"
        open={mobileOpen}
        onClose={() => setMobileOpen(false)}
        PaperProps={{
          sx: {
            width: 240,
            bgcolor: theme.palette.primary.main,
            color: 'white',
          },
        }}
      >
        <List>
          {navigationItems.map((item) => (
            <ListItem
              button
              key={item.path}
              onClick={() => handleNavigation(item.path)}
              selected={currentPath === item.path}
              sx={{
                '&.Mui-selected': {
                  bgcolor: 'rgba(255, 255, 255, 0.1)',
                  '&:hover': {
                    bgcolor: 'rgba(255, 255, 255, 0.2)',
                  },
                },
                '&:hover': {
                  bgcolor: 'rgba(255, 255, 255, 0.1)',
                },
              }}
            >
              <ListItemIcon sx={{ color: 'white' }}>{item.icon}</ListItemIcon>
              <ListItemText
                primary={item.label}
                primaryTypographyProps={{
                  fontFamily: '"Trade Gothic", Arial, sans-serif',
                  fontWeight: 500,
                }}
              />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </>
  );

  const renderDesktopNav = () => (
    <Box sx={{ borderBottom: 1, borderColor: 'divider', bgcolor: 'white' }}>
      <Tabs
        value={currentPath}
        onChange={(_, path) => handleNavigation(path)}
        variant="fullWidth"
        textColor="primary"
        indicatorColor="primary"
        aria-label="navigation tabs"
        sx={{
          '& .MuiTab-root': {
            fontFamily: '"Trade Gothic", Arial, sans-serif',
            fontWeight: 600,
            fontSize: '1rem',
            textTransform: 'none',
            minHeight: 48,
            py: 1,
          },
        }}
      >
        {navigationItems.map((item) => (
          <Tab
            key={item.path}
            value={item.path}
            label={
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                {item.icon}
                {item.label}
              </Box>
            }
          />
        ))}
      </Tabs>
    </Box>
  );

  return isMobile ? renderMobileNav() : renderDesktopNav();
};

export default Navigation;